import { useStaticQuery, graphql } from "gatsby";
import { mapCraft } from "./mappings";

const query = graphql`
  {
    craft {
      entry(section: "about") {
        ... on Craft_about_about_Entry {
          title
          body
          description
        }
      }
    }
  }
`;

export const useCraft = () => {
  const { craft } = useStaticQuery(query);
  return mapCraft(craft);
};
